.page {
  background-color: #f0f4f8;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
}

/* Top banner */
.cover {
  width: 100%;
  height: 350px;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay {
  backdrop-filter: blur(4px);
  background-color: rgba(0, 0, 0, 0.55);
  padding: 30px;
  border-radius: 12px;
  text-align: center;
  color: white;
  max-width: 90%;
}

.coverTitle {
  color: #fff;
  font-size: 36px;
  font-weight: bold;
  margin: 0;
}

.companyName {
  font-size: 20px;
  font-weight: 300;
  margin-top: 8px;
}

/* Details section */
.details {
  min-height: 500px;
  position: relative;
  top: -90px;
  max-width: 900px;
  margin: 40px auto;
  padding: 30px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}

.details h2 {
  font-size: 24px;
  color: #2c3e50;
  margin-bottom: 16px;
}

.details p {
  font-size: 16px;
  color: #444;
  line-height: 1.6;
  /* margin-bottom: 24px; */
}

/* Info grid */
.infoGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 20px;
}

.infoBox {
  background-color: #f8fafc;
  border: 1px solid #e1e5eb;
  border-radius: 8px;
  padding: 16px;
  text-align: center;
}

.infoBox h4 {
  font-size: 16px;
  color: #333;
  margin-bottom: 6px;
}

.infoBox p {
  font-size: 15px;
  color: #555;
}

/* Input styles */
.infoBox input[type="text"] {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #e1e5eb;
  border-radius: 6px;
  font-size: 15px;
  color: #2c3e50;
  background-color: white;
  transition: all 0.3s ease;
  margin-top: 8px;
}

.infoBox input[type="text"]:focus {
  outline: none;
  border-color: #0077cc;
  box-shadow: 0 0 0 2px rgba(0, 119, 204, 0.1);
}

.infoBox input[type="text"]::placeholder {
  color: #a0aec0;
}

.infoBox input[type="radio"] {
  accent-color: #0077cc;
  cursor: pointer;
}

/* Fallback message */
.message {
  text-align: center;
  padding: 100px;
  font-size: 18px;
  color: #888;
}
.buttonWrapper {
grid-column: 1 / -1;
display: flex;
justify-content: center;
margin-top: 20px;
}

.orderButton {
padding: 12px 24px;
background-color: #0077cc;
color: white;
border: none;
border-radius: 8px;
font-size: 16px;
font-weight: 500;
cursor: pointer;
transition: background-color 0.3s ease;
}

.orderButton:hover {
background-color: #005fa3;
}
