.container-centers-centers {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #f0f4f8, #ffffff);
  padding-top: 3rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.projectTitle {
  font-size: 2rem;
  font-weight: 700;
  color: #1e293b;
}

.subtitle {
  font-size: 1rem;
  color: #6b7280;
}

.formWrapper {
  background-color: white;
  padding: 2.5rem 2rem;
  border-radius: 1rem;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  width: 90%;
  max-width: 420px;
  animation: fadeIn 0.5s ease-in-out;
}

.title {
  font-size: 1.75rem;
  font-weight: 600;
  color: #111827;
  text-align: center;
  margin-bottom: 0.5rem;
}

.description {
  text-align: center;
  font-size: 0.95rem;
  color: #6b7280;
  margin-bottom: 1.5rem;
}

.button {
  display: inline-block;
  padding: 0.6rem 1.6rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 1rem;
}

.buttonBlue {
  background-color: #3b82f6;
}

.buttonBlue:hover {
  background-color: #2563eb;
}

.buttonGreen {
  background-color: #10b981;
}

.buttonGreen:hover {
  background-color: #059669;
}

/* Subtle fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.login-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 2rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.login-form h2 {
  text-align: center;
  margin-bottom: 1rem;
  color: #343a40;
}

.login-form form div {
  margin-bottom: 15px;
}

.login-form label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.login-form input {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.login-form button {
  width: 100%;
  padding: 0.75rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: bold;
}

.login-form button:hover {
  background-color: #0056b3;
}

.toggle-mode-btn {
  margin-top: 10px;
  background-color: transparent;
  color: #007bff;
  border: none;
  cursor: pointer;
  text-decoration: underline;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.inputGroup {
  display: flex;
  flex-direction: column;
}

.label {
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #374151; /* Gray text */
}

.input,
.select {
  padding: 0.5rem;
  border: 1px solid #d1d5db; /* Light gray border */
  border-radius: 0.375rem;
  font-size: 1rem;
}

.input:focus,
.select:focus {
  outline: none;
  border-color: #3b82f6; /* Blue border on focus */
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2); /* Blue shadow */
}

.button {
  padding: 0.75rem;
  border: none;
  border-radius: 0.375rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
}

.buttonPrimary {
  background-color: #3b82f6; /* Blue */
  color: white;
}

.buttonSecondary {
  background-color: #f3f4f6; /* Light gray */
  color: #374151; /* Gray text */
}

.buttonPrimary:hover {
  background-color: #2563eb; /* Darker blue */
}

.buttonSecondary:hover {
  background-color: #e5e7eb; /* Darker light gray */
}
